import { Component, h, Prop, Element, State } from '@stencil/core';
import interact from 'interactjs';
import { EVENTS_TYPES } from '../../utils/utils';
import { AddonService } from '../../services/service';

@Component({
  tag: 'neppo-chat',
  styleUrl: 'neppochat-component.css',
  shadow: true,
})
export class NeppoChatComponent {
  private neppoURL = '';
  private neppoToken = '';
  private addonService = new AddonService();
  private iframeHtml = '';
  @State() isDragging = false;
  @State() isWidgetOpen = false;
  @State() widgetPosition: 'left' | 'right' = 'left';

  @Element() private element: HTMLElement;
  /**
   * Parameters
   */
  @Prop() parameters: string;

  private createStatusOpeningListener(iframe: HTMLIFrameElement, event: string) {
    iframe.contentWindow.addEventListener(event, (msg: any) => {
      if (msg.detail.status == 'snippet-opening') {
        this.openChat();
      }
      if (msg.detail.status == 'snippet-closed') {
        this.closeChat();
      }
    });
  }

  private createLoadListener(iframe: HTMLIFrameElement, event: string) {
    iframe.contentWindow.addEventListener(event, (msg: any) => {
      if (typeof msg.detail.loadedSuccessfully === 'boolean') {
        setTimeout(() => {
          this.addEventButtonHover(iframe);
        }, 300);
      }
    });
  }

  private openChat() {
    const div = this.element.shadowRoot.querySelector('.neppo-chat-main-div');
    div.classList.add('maximized-chat');
    div.classList.remove('minimized-chat');
    this.isWidgetOpen = true;
  }

  private closeChat() {
    const div = this.element.shadowRoot.querySelector('.neppo-chat-main-div');
    div.classList.add('minimized-chat');
    div.classList.remove('maximized-chat');
    this.isWidgetOpen = false;
  }

  private addEventButtonHover(iframe: HTMLIFrameElement) {
    const iframeDoc = iframe.contentWindow?.document;
    const icon = this.element.shadowRoot.querySelector('#drag-icon') as HTMLElement;

    if (!iframeDoc && !icon) {
      console.warn('[NeppoInfo] Could not access iframe content');
      return;
    }

    let hoveringButton = false;
    let hoveringIcon = false;

    const showIcon = () => {
      icon.style.display = 'flex';
    };

    const hideIcon = () => {
      if (!hoveringButton && !hoveringIcon && !this.isDragging) {
        icon.style.display = 'none';
      }
    };

    const targetElement = iframeDoc.querySelector('#chat-agent-main > app-widget > div > div > button');

    if (targetElement) {
      targetElement.addEventListener('mouseenter', () => {
        hoveringButton = true;
        showIcon();
      });

      targetElement.addEventListener('mouseleave', () => {
        hoveringButton = false;
        hideIcon();
      });

      icon.addEventListener('mouseenter', () => {
        hoveringIcon = true;
        showIcon();
      });

      icon.addEventListener('mouseleave', () => {
        hoveringIcon = false;
        hideIcon();
      });
    } else {
      console.warn('[NeppoInfo] Element not found inside iframe');
    }
  }

  private adjustLayout(iframe: HTMLIFrameElement) {
    const div = this.element.shadowRoot.querySelector('.neppo-chat-main-div') as HTMLElement;
    div.style.visibility = 'hidden';
    div.style.opacity = '0';
    const widgetContainerElement = iframe.contentWindow.document.getElementById('widget-container');
    const element = iframe.contentWindow.document.querySelector('#chat-agent-main > app-widget > div') as HTMLElement;
    if (!widgetContainerElement || !element) {
      window.setTimeout(() => {
        this.adjustLayout(iframe);
      }, 1000);
      return;
    }

    const configEl = iframe.contentWindow.document.querySelector('.neppo-wrapper-config');
    if (configEl) {
      configEl.remove();
    }

    widgetContainerElement.style.height = '85vh';
    element.style['align-items'] = 'baseline';
    element.style['right'] = 'auto';
    div.style.visibility = 'visible';
    div.style.opacity = '1';
    div.style.transition = 'visibility 0s linear 0s, opacity 300ms, width linear 500ms';
  }

  private updateIframeAlignment(position: 'left' | 'right', container: HTMLElement, target: HTMLElement) {
    const iframe = this.element.shadowRoot?.querySelector('#neppo-chat-agent-iframe') as HTMLIFrameElement;
    if (!iframe?.contentWindow?.document) return;

    const iframeDoc = iframe.contentWindow.document;
    const button = iframeDoc.querySelector('#chat-agent-main > app-widget > div') as HTMLElement;
    if (!button) return;

    const rect = container.getBoundingClientRect();
    const currentY = parseFloat(target.getAttribute('data-y')) || 0;

    if (position === 'left') {
      container.style.left = `${rect.left}px`;
      container.style.right = 'auto';
    } else {
      const rightOffset = window.innerWidth - rect.right;
      container.style.right = `${rightOffset}px`;
      container.style.left = 'auto';
    }

    target.style.transform = `translateY(${currentY}px)`;
    target.setAttribute('data-x', '0');
    target.setAttribute('data-y', currentY.toString());
  }

  private clampBackToViewport(container: HTMLElement, target: HTMLElement) {
    const rect = container.getBoundingClientRect();
    const { innerWidth: windowWidth, innerHeight: windowHeight } = window;

    const visibility = this.calculateVisibility(rect, windowWidth, windowHeight);

    const { currentX, currentY } = this.getTransformPosition(target);
    const { correctedX, correctedY } = this.clampPosition(rect, currentX, currentY, windowWidth, windowHeight);

    const hasChanged = correctedX !== currentX || correctedY !== currentY;

    if (hasChanged) {
      this.applyTransform(target, correctedX, correctedY);
    }

    if (visibility < 50) {
      this.reanchorWidgetToOrigin(container, target, rect);
    }
  }

  private calculateVisibility(rect: DOMRect, windowWidth: number, windowHeight: number): number {
    const totalArea = rect.width * rect.height;
    const visibleWidth = Math.min(rect.right, windowWidth) - Math.max(rect.left, 0);
    const visibleHeight = Math.min(rect.bottom, windowHeight) - Math.max(rect.top, 0);
    const visibleArea = Math.max(0, visibleWidth) * Math.max(0, visibleHeight);
    return totalArea > 0 ? (visibleArea / totalArea) * 100 : 0;
  }

  private getTransformPosition(target: HTMLElement) {
    return {
      currentX: parseFloat(target.getAttribute('data-x')) || 0,
      currentY: parseFloat(target.getAttribute('data-y')) || 0,
    };
  }

  private clampPosition(rect: DOMRect, currentX: number, currentY: number, windowWidth: number, windowHeight: number) {
    let correctedX = currentX;
    let correctedY = currentY;

    if (rect.top < 0) correctedY -= rect.top;
    if (rect.bottom > windowHeight) correctedY -= rect.bottom - windowHeight;
    if (rect.left < 0) correctedX -= rect.left;
    if (rect.right > windowWidth) correctedX -= rect.right - windowWidth;

    return { correctedX, correctedY };
  }

  private applyTransform(target: HTMLElement, x: number, y: number) {
    target.style.transform = `translateX(${x}px) translateY(${y}px)`;
    target.setAttribute('data-x', x.toString());
    target.setAttribute('data-y', y.toString());
  }

  private reanchorWidgetToOrigin(container: HTMLElement, target: HTMLElement, rect: DOMRect) {
    this.applyTransform(target, 0, 0);

    container.style.bottom = '0';
    container.style.top = 'auto';

    const windowWidth = window.innerWidth;
    const windowCenter = windowWidth / 2;
    const elementCenter = rect.left + rect.width / 2;

    this.widgetPosition = elementCenter < windowCenter ? 'left' : 'right';

    const icon = this.element.shadowRoot?.querySelector('#drag-icon') as HTMLElement;
    const iframe = this.element.shadowRoot?.querySelector('#neppo-chat-agent-iframe') as HTMLIFrameElement;
    const iframeDoc = iframe?.contentWindow?.document;
    const button = iframeDoc?.querySelector('#chat-agent-main > app-widget > div') as HTMLElement;

    if (!button || !icon) return;

    button.style['transition'] = 'align-items 300ms ease-in-out';
    icon.style.justifyContent = this.widgetPosition === 'left' ? 'flex-start' : 'flex-end';

    if (this.widgetPosition === 'left') {
      button.style['align-items'] = 'baseline';
      button.style['right'] = 'auto';
      container.style.left = '0';
      container.style.right = 'auto';
    } else {
      button.style['align-items'] = 'flex-end';
      button.style['left'] = 'auto';
      button.style.removeProperty('right');
      container.style.right = '0';
      container.style.left = 'auto';
    }
  }

  async componentWillLoad() {
    try {
      const userData = await this.addonService.getUserData();
      if (userData.status == 200) {
        this.neppoURL = userData.data.responseBody.neppoURL + '/chat';
        this.neppoToken = userData.data.responseBody.neppoToken;
      }
    } catch (ex) {
      console.debug("can't get userData", ex);
    }
    if (this.neppoToken == '' || this.neppoURL == '') {
      return;
    }
    return fetch(this.neppoURL + '/chat-agent/index.html')
      .then((response: Response) => response.text())
      .then(response => {
        response = response.replace('<base href="/chat/chat-agent/">', `<base href="${this.neppoURL}/chat-agent/"`);
        response = response.replace('window.neppoHost = "http://localhost:8080/chat"', `window.neppoHost = "${this.neppoURL}"`);
        response = response.replace('window.neppoToken = ""', `window.neppoToken = "${this.neppoToken}"`);
        this.iframeHtml = response;
      });
  }

  componentDidLoad() {
    const container = (this.element.shadowRoot || this.element).querySelector('#neppo-chat-agent') as HTMLElement;
    const dragTarget = container?.querySelector('#drag-icon')?.parentElement as HTMLElement;

    if (!container || !dragTarget) {
      console.warn('[NeppoInfo] Container or Drag Target not found');
      return;
    }

    dragTarget.style.transition = 'transform 300ms ease';

    interact(container).draggable({
      allowFrom: '#drag-icon',
      inertia: false,
      listeners: {
        start: () => {
          this.isDragging = true;
          const icon = this.element.shadowRoot?.querySelector('#drag-icon') as HTMLElement;
          if (icon) {
            icon.style.height = '100%';
          }
        },
        move: event => {
          const target = event.target as HTMLElement;
          const x = (parseFloat(target.getAttribute('data-x')) || 0) + event.dx;
          const y = (parseFloat(target.getAttribute('data-y')) || 0) + event.dy;

          target.style.transform = `translate(${x}px, ${y}px)`;
          target.setAttribute('data-x', x.toString());
          target.setAttribute('data-y', y.toString());
        },
        end: event => {
          const target = event.target as HTMLElement;
          const container = this.element.shadowRoot?.querySelector('#neppo-chat-agent') as HTMLElement;
          const icon = this.element.shadowRoot?.querySelector('#drag-icon') as HTMLElement;

          if (!container || !target) {
            console.warn('[NeppoInfo] Container elements or Drag Target not found');
            return;
          }

          if (icon) icon.style.height = '20px';
          this.isDragging = false;

          const currentY = parseFloat(target.getAttribute('data-y')) || 0;

          this.updateIframeAlignment(this.widgetPosition, container, target);

          target.style.transform = `translateY(${currentY}px)`;
          target.setAttribute('data-x', '0');
          target.setAttribute('data-y', currentY.toString());

          setTimeout(() => {
            this.clampBackToViewport(container, container);
          }, 500);
        },
      },
    });
  }

  render() {
    if (this.neppoToken == '' || this.neppoURL == '') {
      return;
    }

    return (
      <div id="neppo-chat-agent" class="neppo-chat-main-div minimized-chat" style={{ ['left']: '0' }}>
        <iframe
          id="neppo-chat-agent-iframe"
          class="neppo-main-iframe"
          sandbox="allow-same-origin allow-scripts allow-forms allow-popups allow-downloads"
          allow="autoplay; camera; microphone"
          scrolling="no"
          srcdoc={this.iframeHtml}
          onLoad={ev => this.onLoadIframe(ev)}
        ></iframe>
        <div id="drag-icon" class="drag-icon">
          
        </div>
      </div>
    );
  }

  onLoadIframe(ev: Event) {
    this.createStatusOpeningListener(ev.target as HTMLIFrameElement, EVENTS_TYPES.STATUS_OPENING_SNIPPED);
    this.createLoadListener(ev.target as HTMLIFrameElement, EVENTS_TYPES.LOADED_APPLICATION);
    this.adjustLayout(ev.target as HTMLIFrameElement);
  }
}
