:host {
  display: block;
}

.neppo-chat-main-div {
  display: flex;
  flex-direction: column-reverse;
  border: 0;
  background-color: transparent;
  pointer-events: auto;
  z-index: 2147483639;
  position: fixed;
  bottom: 0;
  overflow: hidden;
  opacity: 1;
  max-height: 100%;
  touch-action: none;
  user-select: none;
}

.drag-icon {
  display: none;
  align-items: end;
  background: transparent;
  cursor: move;
  user-select: none;
  flex-shrink: 0;
  height: 20px;
  z-index: 1;
  transition: transform 300ms ease;
}

.minimized-chat {
  width: 90px;
  height: 90px;
  transition: width 500ms;
}

.minimized-chat:hover.neppo-chat-main-div {
  width: 400px;
}

.maximized-chat {
  width: 400px;
  height: 85vh;
}

.neppo-main-iframe {
  background: none;
  border: 0px;
  float: none;
  position: absolute;
  inset: 0px;
  width: 100%;
  height: 100%;
  margin: 0px;
  padding: 0px;
  min-height: 0px;
}
